.switch-button {
  background: transparent;
  display: flex;
  cursor: pointer;
  border-radius: 50px;
  .button-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50px;
    padding: 2px;
    gap: 4px;
    position: relative;
    transition: 0.3s ease-in-out;
    .ball {
      width: 29px;
      height: 29px;
      border-radius: 50%;
      position: absolute;
      &:hover {
        transform: scale(1.1);
      }
      transition: 0.3s ease-in-out;
    }
    .dark {
      right: -1px;
      top: -1px;
    }
    .light {
      left: -1px;
      top: -1px;
    }
  }
}
