@font-face {
  font-family: "JosefinSans-Regular";
  src: url(./assets/fonts/JosefinSans-Regular.ttf);
}
@font-face {
  font-family: "Caveat";
  src: url(./assets/fonts/Caveat-Regular.ttf);
}

$font-1: "JosefinSans-Regular", serif;
$font-2: "Caveat", serif;

$color-1: #4dabf5;
$color-2: #ffd0c4;
$color-3: rgb(0, 22, 44);
$color-4: #fdeeee;
$black: rgb(40, 40, 40);
$white: rgb(253, 253, 253);
$shadow: rgb(219, 218, 218);

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style-type: none;
  outline: none;
  border: none;
  text-decoration: none;

  &::selection {
    background: $color-1;
    color: $white;
  }
}

html,
body,
#root {
  min-height: 100vh;
  font-family: $font-1;
  line-height: 1.4rem;
  background: $white;
  color: $color-3;
  scroll-behavior: smooth;
}
